import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'

Vue.use(Vuex);

const instance = Axios.create({
  baseURL: 'https://localhost:5001/api/',
  //baseURL: 'https://mivueapi.azurewebsites.net/api/',
  headers: {'Content-Type': 'application/json;charset=UTF-8'}
});

function convertDates(conversation) {
  conversation.startedOn = new Date(conversation.startedOn)

  conversation
    .messages
    .forEach(message => {
      message.sentOn = new Date(message.sentOn)
    });
  return conversation
}

export const store = new Vuex.Store({
  state: {
    conversations: [],
    statusFilter: '',
    users: [],
    currentUser: {
      "id": "7fc5c7c1-864b-498d-93c7-08d676ea4453",
      "firstName": "Handler",
      "lastName": "User",
      "outOfOffice": null
    }
  },
  getters: {
    ALL_CONVERSATIONS: state => {
      return state.conversations
    },
    CONVERSATIONS_BY_STATUS: (state) => (status) => {
      return state.conversations.filter((conversation) =>
        conversation.currentStatus.toLowerCase() === status.toLowerCase()
      )
    },
    CONVERSATION_BY_ID: (state) => (id) => {
      return state.conversations.find(conversation => conversation.id === id)
    },
    ALL_USERS: state => {
      return state.users
    },
    USER_BY_ID: (state) => (id) => {
      return state.users.find(user => user.id === id)
    },
    CURRENT_USER: (state) => {
      return state.currentUser
    },
    SELECTED_EXPERT: (state) => {
      return state.users.find(user => user.id === '18b1d119-fdce-4b02-93c8-08d676ea4453')
    }
  },
  mutations: {
    ADD_CONVERSATION: (state, payload) => {
      payload = convertDates(payload)
      state.conversations.push(payload)
    },
    REMOVE_CONVERSATION: (state, payload) => {
      let conversationIndex = state
        .conversations
        .findIndex(c => c.Id === payload.Id)
      
      // Splice removes the last element in the array if the index is not found 
      // which would when the conversation to remove is not found.
      // Code below prevents this from happening.
      if(conversationIndex >= 0){
        state.conversations.splice(conversationIndex, 1)
      }
    },
    REMOVE_ALL_CONVERSATIONS: (state) => {
      state.conversations = []
    },
    ADD_USER: (state, payload) => {
      state.users.push(payload)
    },
    REMOVE_ALL_USERS: (state) => {
      state.users = []
    },
    SET_STATUS_FILTER: (state, payload) => {
      state.statusFilter = payload
    }
  },
  actions: {
    getConversations: async (context) => {
      instance.get('conversations')
        .then(function (response){
          context.commit('REMOVE_ALL_CONVERSATIONS')
          response.data.forEach(conversation => 
            context.commit('ADD_CONVERSATION', conversation))
        })
        .catch()
        .finally()      
    },
    addConversation: async (context, data) => {
      instance.post('conversations', data)
        .then(response => context.commit('ADD_CONVERSATION', response.data))
        .catch()
        .finally()
    },
    addMessage: async (context, data) => {
      instance.post('messages', data)
        .then(() => {
          context.getters.CONVERSATION_BY_ID(data.conversationId).messages.push(data)    
        })
        .catch()
        .finally()      
    },
    getUsers: async (context) => {
      instance.get('users')
        .then(response => {
          context.commit('REMOVE_ALL_USERS')
          response.data.forEach(user =>
            context.commit('ADD_USER', user))
        })
        .catch()
        .finally()
    }
  }
})