<template>
  <div class="main">
    <v-content>
      <v-container 
        v-show="isLoading"
        bg
        fill-height
        grid-list-md
        text-xs-center
      >
        <v-layout 
          row
          wrap
          align-center 
        >
          <v-flex>
            <v-progress-circular 
              indeterminate 
              color="primary"
              id="main-spinner"
            ></v-progress-circular>
          </v-flex>
        </v-layout>
      </v-container>
      <v-list 
        two-line
        v-show="!isLoading"
      >
        <conversation-list-item
          v-for="convo in conversationList"
          :key="convo.id"
          :conversation="convo"
        ></conversation-list-item>
      </v-list>
      <v-expand-transition>
        <router-view></router-view>
      </v-expand-transition>
    </v-content>
  </div>
  
</template>
<script>
import ConversationListItem from '../components/ConversationListItem.vue'

export default {
  data: () => ({
    isLoading: true
  }),
  components: {
    ConversationListItem
  },
  mounted: function () {
    this.isLoading = true
    this.$store.dispatch('getUsers')
    this.$store.dispatch('getConversations')
  },
  computed: {
    conversationList() {
      // REWORK: Following line shouldn't be here but is needed to prevent 
      // infinite loading
      this.isLoading = false

      let status = this.$route.params.status
      
      if (status !== undefined
        && status !== ''
        && status !== 'all')
      {
        return this.$store.getters.CONVERSATIONS_BY_STATUS(status)
      }
      return this.$store.getters.ALL_CONVERSATIONS
    },
    userList() {
      return this.$store.getters.ALL_USERS
    }
  }
}
</script>
<style scoped>
  #main-spinner{
    margin-top: 5em;
  }
</style>
