<template>
  <div>
    <v-list-tile
      @click ="showConversation = !showConversation"
    >
      <v-list-tile-content>
        <v-list-tile-title>
          <span class="font-weight-bold">{{ conversation.title }}</span>
          <span class="grey--text text--darken-1 font-weight-light font-italic">&nbsp;{{ conversation.internalReference }}</span>
        </v-list-tile-title>
        <v-list-tile-sub-title>
          {{ getFullUserName(getLastMessage(conversation.messages).sentBy) }}
          &nbsp;&mdash;&nbsp;
          <span class="grey--text">{{ getLastMessage(conversation.messages).body }}</span>
        </v-list-tile-sub-title>
      </v-list-tile-content>
    </v-list-tile>
    
    <v-expand-transition>
      <conversation
        v-show="showConversation"
        :conversation="conversation"
      ></conversation>
    </v-expand-transition>
    <v-divider></v-divider>
  </div>
</template>

<script>
import Conversation from './Conversation.vue'

export default {
  data: () => ({
    showConversation: false
  }),
  props: {
    conversation: Object
  },
  components: {
    Conversation
  },
  methods: {
    getLastMessage: function (messages) {
      return messages[messages.length - 1];
    },
    getFullUserName: function (userId) {
      let user = this.$store.getters.USER_BY_ID(userId)
      return `${user.firstName} ${user.lastName}`
    },
  }
}
</script>
